export default (fields, expected) => {
  const values = Object.keys(fields).map(key => fields[key]);

  if (values.includes('')) {

    return { status: false, 
      error: 'empty', 
      errorInputs: Object.keys(fields).filter(key => !fields[key]), 
      msg: 'Необходимо заполнить все поля'}
  } else {
    
    return { status: true, 
      error: '', 
      errorInputs: [],
      msg: ''}
  }
}