import React, { PureComponent } from 'react';
import SectionTitle from '../SectionTitle';
import TaskList from '../TaskList';
import NewTaskForm from '../NewTaskForm';
import Cap from '../Cap';
import Preloader from '../Preloader';
import { CSSTransition } from 'react-transition-group';
import api from '../../api';
import styles from './Dashboard.module.css';

class Dashboard extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      curTaskItem: {},
      tasks: [],
      newTaskWindow: false,
      isLoaded: false,
    }
  }

  async componentDidMount() {
    const { userId } = this.props;
    const tasks = await api.getTasks(userId);

    this.setState(prevState => ({ ...prevState, tasks, isLoaded: true }));
  }

  openTaskWindow = (mode, data) => {
    this.setState(prevState => ({ 
      ...prevState,
      newTaskWindow: true,
      curTaskItem: data,
    }));
  }

  closeTaskWindow = () => {
    this.setState(prevState => ({ 
      ...prevState,
      newTaskWindow: false,
    }));
  }

  deleteTask = async taskId => {
    const tasks = await api.deleteTask(taskId);

    this.setState(prevState => ({ 
      ...prevState, 
      tasks
    }));
  }

  updateTask = async (taskId, taskData) => {
    const updateAnswer = await api.updateTask(taskId, taskData);

    this.setState(prevState => ({ 
      ...prevState, 
      tasks: updateAnswer.tasks
    }));

    return updateAnswer;
  }

  render() {
    const { userPermission, userName } = this.props
    const { 
      isLoaded, 
      newTaskWindow, 
      tasks, 
      curTaskItem 
    } = this.state;

    return isLoaded
      ? <section className={styles.sectionContainer}>
          <SectionTitle 
            children={userPermission === 'manager' ? 'Список задач' : 'Мои задачи'} 
            size={userPermission === 'manager' ? 's' : 'm'} />
          {
            userPermission === 'manager'
              ? <span className={styles.subtitle}>{`Сотрудник: ${userName}`}</span>
              : null
          }
          {
            tasks.length
              ? <TaskList list={tasks} 
                  userPermission={userPermission}
                  activities={{ 
                    updateTask: this.updateTask, 
                    deleteTask: this.deleteTask,
                    openTaskWindow: this.openTaskWindow.bind(null, 'update') }}/>
              : <Cap text='Текущих задач не найдено'/>
          }
          <CSSTransition
            in={newTaskWindow}
            timeout = {300}
            mountOnEnter={true}
            unmountOnExit={true}
            classNames={{
              appear: styles.appear,
              enter: styles.enter,
              enterActive: styles.enterActive,
              exit: styles.exit,
              exitActive: styles.exitActive,
            }}>
              <NewTaskForm 
                mode='update' 
                taskData={curTaskItem} 
                updateTask={this.updateTask} 
                onClose={this.closeTaskWindow}/>
          </CSSTransition>
        </section>
      : <div className={styles.preloaderBlock}>
        <Preloader/>
      </div> 
  }
}

export default Dashboard;