import React, { PureComponent } from 'react';
import api from '../../api';
import deleteCookie from './utils/cookies';
import validate from './utils/validate';

const { Provider, Consumer: AuthConsumer } = React.createContext('');

class AuthProvider extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {
      login: '',
      password: '',
      user: null,
      isLoaded: false,
      authorization: {
        status: false,
        msg: '',
        error: '',
        errorInputs: []
      }
    }
  }

  async componentDidMount() {
    const user = await api.authFromToken();

    this.setState({
      user: user || null,
      isLoaded: true,
      authorization: {
        ...this.state.authorization,
        status: !!user
      }
    })
  }

  authorize = async ({ login, password, remembered }) => {
    let authorization, user;
    let validation = validate({ login, password });
    
    if (validation.status) {
      user = await api.logIn({ email: login, password, remembered });
      console.log(user);
      
      authorization = user
        ? { status: true, 
          error: '',
          errorInputs: [],
          msg: 'Вы успешно авторизовались!' }
        : { status: false, 
          error: 'wrong',
          errorInputs: ['login', 'password'],
          msg: 'Неверные логин или пароль' }
    } else {
      authorization = validation;
    }

    this.setState({
      login,
      password,
      user: user ? user : null,
      authorization
    });

    return authorization;
  }

  logout = async () => {
    deleteCookie('access_token');

    this.setState({
      login: '',
      password: '',
      user: null,
      authorization: {
        status: '',
        msg: '',
        error: '',
        errorInputs: []
      }
    });
  }

  getAuthData = () => ({
    login: this.state.login,
    password: this.state.password,
    isLoaded: this.state.isLoaded,
    user: this.state.user,
    authorize: this.authorize,
    logout: this.logout,
    authorization: this.state.authorization,
  })

  render() {
    const { children } = this.props;
    return <Provider value={this.getAuthData()}>{children}</Provider>
  }
}

export { AuthProvider, AuthConsumer };