import React, { PureComponent } from 'react';
import Input from '../Input';
import Button from '../Button';
import FormMsg from '../FormMsg';
import Checkbox from '../Checkbox';
import fieldsData from './fieldsData';
import styles from './LoginForm.module.css';

class Form extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        login: '',
        password: '',
        remembered: ''
      },
      authorization: props.authorization
    }

    this.activeInput = React.createRef();
  }

  componentDidMount() {
    this.activeInput.current.focus();
  }

  toggleCheckbox = e => {
    const { fields } = this.state;
    const remembered = e.target.checked.toString();

    this.setState(prevState => ({ 
      ...prevState,
      fields: { ...fields, remembered },
    }));
  }

  onInputChange = e => {
    const { fields, authorization } = this.state;

    this.setState({ 
      fields: { ...fields, [e.target.name]: e.target.value },
      authorization: {
        ...authorization,
        errorInputs: authorization.errorInputs.filter(input => input !== e.target.name)
      } 
    });
  }

  onSubmit = async e => {
    e.preventDefault();

    const { fields } = this.state;
    const { authorize } = this.props;

    const authorization = await authorize(fields);
    
    if (authorization.error === 'wrong') {
      this.resetForm();
    }

    if (!authorization.status) {
      this.activeInput.current.focus();
    }

    this.setState(prevState => ({ ...prevState, authorization }));
  }

  resetForm = () => {
    const { fields } = this.state;

    this.setState({
      fields: Object.keys(fields).reduce((obj, field) => ({ ...obj, [field]: ''}), {})
    });
  }

  render() {
    const { error, msg, errorInputs } = this.state.authorization;
    console.log(this.state.authorization)
    const { fields } = this.state;

    return (
      <div className={styles.formContainer}>
        <div className={styles.formWrap}>
          <form action='/' method='post' 
            className={styles.form} 
            onSubmit={this.onSubmit}
            onReset={this.onReset}>
            {
              fieldsData.map((field, i) => 
                <div  className={`${styles.formItem} ${styles.formItemWide}`} key={field.name}>
                  <Input 
                      type={field.type}
                      name={field.name}
                      ref={errorInputs.length 
                          ? field.name === errorInputs[0] ? this.activeInput : null
                          : i === 0 ? this.activeInput : null}
                      value = {fields[field.name]}
                      placeholder = '. . .'
                      errorClass={errorInputs.includes(field.name) ? error: null}
                      onChange={this.onInputChange}
                  />
                  <label className={styles.formLabel} htmlFor={field.name}>
                    <span>{field.label}</span>
                  </label>
                </div>
              )
            }
            <div  className={`${styles.formItem}`} key='remembered'>
              <Checkbox 
                  type='checkbox'
                  id='remembered'
                  value='remembered'
                  name='remembered'
                  onChange={this.toggleCheckbox}
              />
               <label className={styles.formLabel} htmlFor='remembered'>
                <span>Запомнить меня</span>
              </label>
            </div>
            <div className={styles.msgBlock}>
              <FormMsg children={msg}/>
            </div>
            <div className={styles.buttonContainer}>
              <Button type='submit' name='submit' children='Войти'/>
            </div>
          </form>
        </div>
      </div>
    )
  }
}

export default Form;