import React, { PureComponent } from 'react';
import AddButton from '../AddButton';
import Textarea from '../Textarea';
import Note from '../Note';
import Preloader from '../Preloader';
import api from '../../api';
import getformattedTime from '../NewTaskForm/utils/formattedTime';
import styles from './Notes.module.css';

class Notes extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        text: '',
      },
      noteList: [],
      animAppear: false,
      isLoaded: false
    }

    this.activeInput = React.createRef();
  }

  async componentDidMount() {
    const { userId } = this.props;

    this.setState({
      fields: { text: '' },
      noteList: await api.getNotes(userId),
      isLoaded: true
    });

    this.activeInput.current.focus();
  }

  onInputChange = e => {
    const { fields } = this.state;

    this.setState({ 
      fields: { ...fields, [e.target.name]: e.target.value }
    });
  }

  saveNewNote = async () => {
    const { text } = this.state.fields;

    if (text) {
      const { notes: noteList } = await api.saveNewNote({ text, date: getformattedTime() });

      this.setState(prevState => ({ fields: { text: '' }, animAppear: true, noteList }))
    }

    setTimeout(this.clearAnim, 1000);
  }

  clearAnim = () => {
    this.setState(prevState => ({ ...prevState, animAppear: false }))
  }

  deleteNote = async taskId => {
    console.log(taskId);
    const noteList = await api.deleteNote(taskId);

    this.setState(prevState => ({ 
      ...prevState, 
      noteList
    }));
  }

  render() {
    const { isLoaded, fields, animAppear, noteList } = this.state;

    return isLoaded
      ? <section className={styles.sectionContainer}>
        <div className={styles.panel}>
          <div className={styles.textBlock}>
            <Textarea 
              ref={this.activeInput}
              type='text'
              name='text'
              value={fields.text}
              onChange={this.onInputChange}
              placeholder='Текст заметки . . .'
              size='big'/>
          </div>
          <div className={styles.addBlock} data-tooltip="Добавить заметку">
            <AddButton onClick={this.saveNewNote}/>
          </div>
        </div>
        {
          noteList.map(note => 
            <Note key={note.id} 
              note={note} 
              animAppear={animAppear} 
              activities={{ deleteNote: this.deleteNote }}/>)
        }
      </section>
    : <Preloader/>
  }
}

export default Notes;