const protocol = window.location.protocol;
const host = window.location.host;

const urls = {
  logIn: `${protocol}//${host}/api/logIn`,
  authFromToken: `${protocol}//${host}/api/authFromToken`,
  saveNewUser: `${protocol}//${host}/api/saveNewUser`,
  deleteUser: `${protocol}//${host}/api/deleteUser`,
  getUsers: `${protocol}//${host}/api/getUsers`,
  getFilterUsers: `${protocol}//${host}/api/getFilterUsers`,
  getUser: `${protocol}//${host}/api/getUser`,
  updateUser: `${protocol}//${host}/api/updateUser`,
  saveUserImage: `${protocol}//${host}/api/saveUserImage`,
  saveNewTask: `${protocol}//${host}/api/saveNewTask`,
  getTasks: `${protocol}//${host}/api/getTasks`,
  updateTask: `${protocol}//${host}/api/updateTask`,
  deleteTask: `${protocol}//${host}/api/deleteTask`,
  saveNewNote: `${protocol}//${host}/api/saveNewNote`,
  getNotes: `${protocol}//${host}/api/getNotes`,
  deleteNote: `${protocol}//${host}/api/deleteNote`,
  getPositions: `${protocol}//${host}/api/getPositions`,
};

const headers = {
  'Accept': 'application/json',
  'Content-Type': 'application/json'
}

const api = {};

api.logIn = async (fields) => {
  const response = await fetch(urls.logIn, {
    headers,
    method: 'post',
    credentials: 'include',
    body: JSON.stringify(fields)
  });

  return response.status !== 200 ? response : response.json();
};

api.authFromToken = async (fields) => {
  const response = await fetch(urls.authFromToken, {
    headers,
    method: 'post',
    credentials: 'include'
  });

  return response.status !== 200 ? response : response.json();
};

api.saveNewUser = async fields => {
  const response = await fetch(urls.saveNewUser, {
    headers,
    method: 'post',
    credentials: 'include',
    body: JSON.stringify(fields)
  });

  return response.status !== 200 ? response : response.json();
};

api.updateUser = async (id, fields) => {
  const response = await fetch(`${urls.updateUser}/${id}`, {
    headers,
    method: 'put',
    body: JSON.stringify(fields)
  });

  return response.status !== 200 ? response : response.json();
};

api.saveUserImage = async (id, formData) => {
  const response = await fetch(`${urls.saveUserImage}/${id}`, {
    method: 'post',
    body: formData
  });

  return response.status !== 200 ? response : response.json();
};

api.deleteUser = async id => {
  const response = await fetch(`${urls.deleteUser}/${id}`, {
    headers,
    credentials: 'include',
    method: 'delete'
  });

  return response.status !== 200 ? response : response.json();
};

api.getUser = async id => {
  const response = await fetch(`${urls.getUser}/${id}`, {
    headers,
    method: 'get',
  });

  return response.status !== 200 ? response : response.json();
};

