import React, { PureComponent } from 'react';
import Button from '../Button';
import Input from '../Input';
import FormMsg from '../FormMsg';
import CloseButton from '../CloseButton';
import { labels, placeholders } from './labels';
import validate from './utils/validate';
import api from '../../api';
import styles from '../Profile/Profile.module.css';
import formStyles from '../LoginForm/LoginForm.module.css';


class ChangePswrdForm extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        oldPassword: '',
        newPassword: '',
        rptPassword: ''
      },
      validation: {
        status: '',
        msg: '',
        error: '',
        errorInputs: []
      }
    }

    this.activeInput = React.createRef();
  }

  componentDidMount() {
    this.activeInput.current.focus();
  }

  onInputChange = e => {
    const { fields, validation } = this.state;

    this.setState({ 
      fields: { ...fields, [e.target.name]: e.target.value }, 
      validation: {
        ...validation,
        errorInputs: validation.errorInputs.filter(input => input !== e.target.name)
      }
    });
  }

  onSubmit = async e => {
    e.preventDefault();

    const { fields } = this.state;
    const { hidePswrdBlock, userId } = this.props;

    const validation = validate(fields);

    if (validation.status) {
      await api.updateUser(
        userId,
        { oldPassword: fields.oldPassword, password: fields.newPassword }
      );
    }

    this.setState({ 
      fields, 
      validation },
        () => {
          validation.status
            ? hidePswrdBlock()
            : this.activeInput.current.focus()
        });
  }
  
  render() {
    const { fields, validation } = this.state;
    const { hidePswrdBlock } = this.props;
    const { msg, error, errorInputs } = validation;
  
    return <form action='/' className={styles.formChangePswrd} onSubmit={this.onSubmit}>
      <div className={styles.closeButtonBlock}>
        <CloseButton onClick={hidePswrdBlock} size='m'/>
      </div>
      <div className={styles.infoBlock}>
        { 
          Object.keys(labels)
            .map((field, i) => 
              <div  className={formStyles.formItem} key={field}>
                <Input type='password'
                    ref={errorInputs.length 
                        ? field === errorInputs[0] ? this.activeInput : null
                        : i === 0 ? this.activeInput : null}
                    errorClass={errorInputs.includes(field) ? error: null}
                    name={field}
                    value={fields[field]}
                    placeholder = {placeholders[field]}
                    onChange={this.onInputChange}/>
                <label className={formStyles.formLabel} htmlFor={field}>
                  <span>{labels[field]}</span>
                </label>
              </div>
            )
          }
      </div>
      <div className={styles.msgBlock}>
        <FormMsg children={msg}/>
      </div>
      <div className={styles.buttonBlock}>
        <Button type='submit' name='submit' children='Изменить'/>
      </div>
    </form>
  }
}

export default ChangePswrdForm;