import React from 'react';
import { AuthProvider, AuthConsumer } from '../../contexts/Auth';
import { BrowserRouter } from 'react-router-dom';
import AppRouter from '../AppRouter';
import LoginForm from '../LoginForm';
import Preloader from '../Preloader';
import Layout from '../Layout';

const App = () =>
  <AuthProvider>
    <BrowserRouter>
      <AuthConsumer>
          {({ user, authorize, authorization, logout, updateUser, isLoaded, }) => {
            return isLoaded
              ? <Layout section={
                  authorization.status
                    ? <AppRouter user={user}/>
                    : <LoginForm 
                        authorize={authorize} 
                        authorization={authorization}/>
                  }
                isAuthorized={authorization.status}
                logout={logout}
                user={user}/>
              : <Preloader/>
          }}
      </AuthConsumer>
    </BrowserRouter>
  </AuthProvider>

export default App;