import React, { PureComponent } from 'react';
import { CSSTransition } from 'react-transition-group';
import SectionTitle from '../SectionTitle';
import ChangePswrdForm from '../ChangePswrdForm';
import PersonalForm from '../PersonalForm';
import Preloader from '../Preloader';
import dateIcon from '../CalendarButton/assets/calendar.svg';
import styles from './Profile.module.css';

class Profile extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      mode: props.mode,
      visibilePswrdBlock: false,
      imgIsReady: false      
    }
  }

  componentDidMount() {
    const icon = new Image();
    icon.src = dateIcon;
    icon.onload = () => this.setState(prevState => ({...prevState, imgIsReady: true }));
  }

  showPswrdBlock = (e) => {
    e.preventDefault();

    this.setState(prevState => ({
      ...prevState,
      visibilePswrdBlock: true  
    }));
  }

  hidePswrdBlock = () => {
    this.setState(prevState => ({
      ...prevState,
      visibilePswrdBlock: false  
    }));
  }

  render() {
    const { visibilePswrdBlock } = this.state;
    const { userId } = this.props;

    return (
        <section className={styles.sectionContainer}>
          <div className={styles.titleBlock}>
            <SectionTitle children='Мой профиль' size='m'/>
            <div className={styles.changePswrd}>
              <a href=' ' className={styles.changePswrdLink} 
                onClick={this.showPswrdBlock} 
                children='Изменить пароль'/>
            </div>
          </div>
          <div className={styles.personalData}>
            <div className={styles.formDataBlock}>
              <PersonalForm 
                userId={userId}
                mode='read'
              />
            </div>
            <CSSTransition
              in={visibilePswrdBlock}
              timeout = {300}
              mountOnEnter={true}
              unmountOnExit={true}
              classNames={{
                appear: styles.appear,
                enter: styles.enter,
                enterActive: styles.enterActive,
                exit: styles.exit,
                exitActive: styles.exitActive,
              }}>
                <div className={styles.changePswrdBlock}>
                  <ChangePswrdForm
                    userId={userId}
                    hidePswrdBlock={this.hidePswrdBlock}/>
                </div>
          </CSSTransition>
          </div>
        </section>
    )
  }
}

export default Profile;