import React, { PureComponent } from 'react';
import CloseButton from '../CloseButton';
import Button from '../Button';
import Input from '../Input';
import Textarea from '../Textarea';
import FormMsg from '../FormMsg';
import validate from './utils/validate';
import getformattedTime from './utils/formattedTime';
import api from '../../api';
import styles from './NewTaskForm.module.css';
import formStyles from '../LoginForm/LoginForm.module.css';

class NewTaskForm extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        task: '',
        comment: ''
      },
      validation: {
        msg: '',
        error: '',
        errorInputs: []
      },
      taskData: {
        executorId: '',
        executorName: ''
      },
      mode: props.mode
    }

    this.activeInput = React.createRef();
  }

  async componentDidMount() {
    let { taskData } = this.props;
    const { mode } = this.state;

    if (mode === 'create') {
      const user = await api.getUser(taskData.executorId);
      taskData = {
        ...taskData,
        executorName: `${user.firstName} ${user.lastName}`
      }
    } 

    const fields = {
      task: taskData.task || '',
      comment: taskData.comment || ''
    }

    this.setState(prevState => ({
      ...prevState,
      taskData,
      fields
    }));

    this.activeInput.current.focus();
  }

  onInputChange = e => {
    const { fields, validation } = this.state;
    
    this.setState({ 
      fields: { ...fields, [e.target.name]: e.target.value}, 
      validation: {
        ...validation,
        msg: '',
        errorInputs: validation.errorInputs.filter(input => input !== e.target.name),
      }
    });
  }

  onSubmit = async e => {
    e.preventDefault();
    
    let serverAnswer;
    const mode = e.target.name;

    const { fields, taskData } = this.state;
    const { updateTask } = this.props;

    const validation = validate(fields);

    if (validation.status && mode === 'create') {
      serverAnswer = await api.saveNewTask({
        task: fields.task,
        comment: fields.comment,
        executorId: taskData.executorId,
        executorName: taskData.executorName,
        date: {
          create: getformattedTime(),
          begin: '',
          done: ''
        }
      });

    console.log(serverAnswer);
    
  
    } else if (validation.status && mode === 'update') {
      serverAnswer = await updateTask(taskData.id, fields);
    }

    
    this.setState(prevState => {
      return { 
          ...prevState, 
          fields: validation.status && mode === 'create'
            ? Object.keys(fields).reduce((obj, field) => ({ ...obj, [field]: ''}), {})
            : Object.keys(fields).reduce((obj, field) => ({ ...obj, [field]: fields[field]}), {}),
          validation: validation.status
            ? { ...validation, msg: serverAnswer.msg }
            : { ...validation }
        }
      },
    () => this.activeInput
      ? this.activeInput.current.focus()
      : null
    );
  }

  render() {
    const { onClose } = this.props;
    const { fields, validation, mode, taskData } = this.state;
    const { errorInputs, error } = validation;

    return <div className={styles.container}>
      <div className={styles.formWrap}>
        <form action='/' method='post' onSubmit={this.onSubmit} name={mode} >
          <span className={styles.title} children={`Сотрудник: ${taskData.executorName}`}/>
          <div className={`${formStyles.formItem} ${formStyles.formItemWide}`} key='task'>
            <Input 
              type='text'
              ref={this.activeInput}
              name='task'
              value={fields['task']}
              readonly={false}
              placeholder='. . .'
              errorClass={errorInputs.includes('task') ? error : null}
              onChange={this.onInputChange}
            />
            <label className={formStyles.formLabel} htmlFor='task'>
              <span>Задача</span>
            </label>
          </div>
          <div className={`${formStyles.formItem} ${formStyles.formItemColumn}`} key='comment'>
            <Textarea
              size='big'
              name='comment'
              value={fields['comment']}
              readonly={false}
              placeholder='. . .'
              onChange={this.onInputChange}
            />
            <label className={formStyles.formLabel} htmlFor='comment'>
              <span>Комментарий к задаче</span>
            </label>
          </div>
          <div className={styles.msgBlock}>
            <FormMsg children={validation.msg}/>
          </div>
          <div className={styles.buttonBlock}>
            {
              mode === 'create'
                ? <Button type='submit' name='create' children='Добавить задачу'/>
                : <Button type='submit' name='update' children='Редактировать'/>
            }
          </div>
        </form>
        <div className={styles.closeButtonBlock}>
          <CloseButton size='s' onClick={onClose}/>
        </div>
      </div>
    </div>
  }
}

export default NewTaskForm;