import React, { PureComponent } from 'react';
import Button from '../Button';
import PersonalPhoto from '../PersonalPhoto';
import Input from '../Input';
import InputDataList from '../InputDataList';
import InputDate from '../InputDate';
import Textarea from '../Textarea';
import FormMsg from '../FormMsg';
import Preloader from '../Preloader';
import { labels, placeholders } from './labels';
import validate from './utils/validate';
import prepareText from './utils/prepareText';
import api from '../../api';
import styles from '../Profile/Profile.module.css';
import formStyles from '../LoginForm/LoginForm.module.css';

const readOnly = ['firstName', 'lastName', 'middleName'];

class PersonalForm extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      fields: {
        lastName: '',
        firstName: '',
        middleName: '',
        position: '',
        birthDate: '',
        phone: '',
        email: '',
        messengers: '',
        image: ''
      },
      positions: [],
      validation: {
        msg: '',
        error: '',
        errorInputs: []
      },
      dialog: {
        calendarIsOpen: false
      },
      mode: props.mode,
      isLoaded: false,
      user: {}
    }

    this.activeInput = React.createRef();
  }

  async componentDidMount() {
    const { userId } = this.props;
    const { mode } = this.state;
    let user;

    if (mode !== 'create' && userId ) {
      user = await api.getUser(userId);
      user.position = user.position.name;
    }

    const positions = await api.getPositions();

    this.setState(prevState =>  {
      const fields = mode === 'create'
        ? Object.keys(prevState.fields).reduce((obj, field) => ({ ...obj, [field]: ''}), {})
        : Object.keys(prevState.fields).reduce((obj, field) => ({ ...obj, [field]: user[field]}), {});

      return {
        ...prevState,
        fields,
        positions,
        isLoaded: true,
        user: user || {}
      }
    });

    if (this.activeInput.current) {
      this.activeInput.current.focus();
    }
  }

  setUpdateMode = () => {
    this.setState(prevState => ({ ...prevState, mode: 'update' }));
  }

  setReadMode = () => {
    this.setState(prevState => ({ ...prevState, mode: 'read' }));
  }

  onInputChange = e => {
    const { fields, validation, dialog } = this.state;
    
    this.setState({ 
      fields: { ...fields, [e.target.name]: e.target.value}, 
      validation: {
        ...validation,
        errorInputs: validation.errorInputs.filter(input => input !== e.target.name),
      },
      dialog
    });
  }

  onInputSelect = (positions, e) => {
    const { fields, validation } = this.state;
    const { value } = e.target;
    
    if (positions.includes(value)) {
      this.setState(prevState => ({ 
        ...prevState,
        fields: { ...fields, [e.target.name]: e.target.value}, 
        validation: {
          ...validation,
          errorInputs: validation.errorInputs.filter(input => input !== e.target.name),
        }
      }));
    }
  }

  onUpdateMode = async e => {
    e.preventDefault();

    await this.setUpdateMode();

    this.activeInput.current.focus();
  }

  openDialog = (name) => {
    this.setState(prevState => ({
      ...prevState,
      dialog: {
        calendarIsOpen: true
      }
    }));
  }

  onPhotoUpload = e => {
    const input = e.target;
    const reader = new FileReader();

    reader.onload = e => {
      this.setState(prevState => ({
        ...prevState,
        fields: {
          ...prevState.fields,
          image: e.target.result
        }
      }));
    }

    if (input.files[0]){
      reader.readAsDataURL(input.files[0])
    }
  }

  closeDialog = () => {
    this.setState(prevState => ({
        ...prevState,
        dialog: {
          calendarIsOpen: false
        }
      }));
  }

  setDate = async (strDate, field) => {
    const { fields, validation } = this.state;

    await this.setState({
      fields: {
        ...fields,
        [field]: strDate
      },
      dialog: {
        calendarIsOpen: false
      },
      validation: {
        ...validation,
        errorInputs: validation.errorInputs.filter(input => input !== field),
      }
    });

    this.closeDialog();
  }

  onSubmit = async e => {
    e.preventDefault();

    const form = e.target;
    const mode = form.name;
    const imgUpload = form.querySelector('#file').files[0];

    const { fields, positions, user } = this.state;

    let newUser;

    const validation = validate(fields, { positions });

    await this.setState(prevState => ({
      ...prevState, 
      dialog: {
        calendarIsOpen: false
      },
      validation 
    }));

    const updateFields = Object.keys(fields)
      .filter(field => fields[field] !== user[field] && field !== 'image');

    if (validation.status && mode === 'create') {
      const pos = positions.find(pos => pos.name === fields.position);
      
      const { user } = await api.saveNewUser({ 
        ...fields, 
        position: { id: pos.id, name: pos.name, permission: pos.permission },
        image: ''
      });

      newUser = user;

    } else if (validation.status && mode === 'update' && updateFields.length) {
        await api.updateUser(
          user.id,
          updateFields
            .reduce((obj, field) => ({...obj, [field]: fields[field]}), {})
        );
    }

    if (imgUpload) {
      await api.saveUserImage(newUser ? newUser.id : user.id, new FormData(form));
    }

    validation.status
      ? this.setReadMode()
      : this.activeInput.current.focus()
  }
  
  render() {
    const { labels, permission } = this.props;
    const { fields, validation, dialog, positions, mode, isLoaded } = this.state;
    const { msg, error, errorInputs } = validation;

    return isLoaded
    ? <form action='/' encType="multipart/form-data" 
        className={styles.formData} 
        onSubmit={this.onSubmit} 
        name={mode}>
      <div className={styles.infoWrap}>
        <div className={styles.infoBlock}>
            {
              Object.keys(fields)
                .filter(field => field !== 'image')
                .map((field, i) => 
                  <div  className={formStyles.formItem} key={field}>
                    {
                      mode === 'read'
                        ? <span className={styles.formValue} 
                            dangerouslySetInnerHTML={{__html: fields[field] 
                              ? prepareText(field, fields[field]) 
                              : ''}}/>
                        : field === 'birthDate'
                          ? <InputDate type='text'
                              name={field}
                              ref={errorInputs[0] === field ? this.activeInput : null}
                              value={fields[field]}
                              readOnly={true}
                              placeholder={placeholders[field]}
                              errorClass={errorInputs.includes(field) ? error : null}
                              onFocus={this.openDialog}
                              isOpen={dialog.calendarIsOpen} 
                              activities={{
                                openDialog: this.openDialog,
                                closeDialog: this.closeDialog,
                                setDate: this.setDate
                              }}/>
                          : field === 'position'
                            ? <InputDataList 
                                name={field}
                                ref={errorInputs[0] === field ? this.activeInput : null}
                                value={fields[field]}
                                placeholder={placeholders[field]}
                                errorClass={errorInputs.includes(field) ? error : null}
                                list={positions} 
                                onChange={this.onInputChange}/>
                            : field === 'messengers'
                              ? <Textarea
                                  ref={errorInputs.length 
                                    ? null
                                    : this.activeInput}
                                  name={field}
                                  value={fields[field]}
                                  placeholder={placeholders[field]}
                                  onChange={this.onInputChange}
                                  onFocus={this.closeDialog}
                                  errorClass={errorInputs.includes(field) ? error : null}
                                />
                              : <Input type='text'
                                  ref={errorInputs.length 
                                    ? field === errorInputs[0] ? this.activeInput : null
                                    : null}
                                  name={field}
                                  value={fields[field]}
                                  readOnly={mode !== 'create' && permission !== 'manager' && readOnly.includes(field)}
                                  placeholder={placeholders[field]}
                                  onChange={this.onInputChange}
                                  onFocus={this.closeDialog}
                                  errorClass={errorInputs.includes(field) ? error : null}
                                />
                    }
                    <label className={formStyles.formLabel} htmlFor={field}>
                      <span>{labels[field]}</span>
                    </label>
                  </div>
                )
              }
        </div>
        <div className={styles.msgBlock}>
          <FormMsg children={msg}/>
        </div>
        <div className={styles.buttonBlock}>
          {
            mode === 'read'
              ? <Button type='button' name='update' onClick={this.onUpdateMode} children='Редактировать'/>
              : mode === 'create'
                ? <Button type='submit' name='create' children='Добавить пользователя'/>
                : <Button type='submit' name='save' children='Сохранить'/>
          }
        </div>
        </div>
        <div className={styles.photoBlock}>
          <PersonalPhoto 
            image={fields.image} 
            mode={mode} 
            onPhotoUpload={this.onPhotoUpload}/>
        </div>
      </form>
      : <Preloader/>
  }
}

const withData = (labels) => (Component) => {
  const wrap = (props) => <Component {...props} labels={labels}/>

  wrap.displayName = `WithData(${getDisplayName(Component)})`

  return wrap;
};

const getDisplayName = (WrappedComponent) => {
  return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

export default withData(labels)(PersonalForm);
