import React, { PureComponent } from 'react';
import { CSSTransition } from 'react-transition-group';
import PersonalForm from '../PersonalForm';
import Input from '../Input';
import AddButton from '../AddButton';
import SectionTitle from '../SectionTitle';
import UserList from '../UserList';
import Popup from '../Popup';
import NewTaskForm from '../NewTaskForm';
import Dashboard from '../Dashboard';
import Cap from '../Cap';
import Preloader from '../Preloader';
import api from '../../api';
import titles from '../../titles.js';
import styles from './Staff.module.css';

class Staff extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      staff: [],
      curStaffItem: {},
      searchFilter: '',
      newTaskWindow: false,
      popup: false,
      popupMsg: '',
      popupAnswer: false,
      view: 'userList',
      viewMode: '',
      isLoaded: false,
    }
  }

  async componentDidMount() {
    const staff = await api.getUsers();

    this.setState(prevState => ({ 
      ...prevState,
      isLoaded: true,
      staff
    }));
  }

  onInputChange = async e => {
    const { value } = e.target;
    let staff;

    if (value) {
      staff = await api.getFilterUsers(value);
    } else {
      staff = await api.getUsers();
    }
      
    this.setState(prevState => ({ 
      ...prevState,
      searchFilter: value, 
      staff
    }));
  }

  deleteUser = async (id) => {
    const staff = await api.deleteUser(id);

    this.setState(prevState => ({ 
      ...prevState,
      staff
    }));

    this.closePopup();
  }

  openPopup = (msg, curStaffItem) => {
    this.setState(prevState => ({ 
      ...prevState,
      popup: true,
      popupMsg: msg || 'popup text',
      curStaffItem
    }));
  }

  closePopup = () => {
    this.setState(prevState => ({ 
      ...prevState,
      popup: false,
    }));
  }

  openTaskWindow = (mode, curStaffItem) => {
    this.setState(prevState => ({ 
      ...prevState,
      newTaskWindow: true,
      curStaffItem,
      viewMode: mode ? mode : ''
    }));
  }

  closeTaskWindow = () => {
    this.setState(prevState => ({ 
      ...prevState,
      newTaskWindow: false,
      viewMode: ''
    }));
  }

  setStaffMode = (view, mode, curStaffItem) => {
    this.setState(prevState => ({ 
      ...prevState,
      curStaffItem,
      view,
      viewMode: mode ? mode : ''
    }));
  }

  returnToList = async e => {
    e.preventDefault();

    const staff = await api.getUsers();

    this.setState(prevState => ({ 
      ...prevState,
      view: 'userList',
      staff 
    }));
  }

  onClickOk = () => {
    this.setState(prevState => ({ 
      ...prevState,
      popupAnswer: true
    }));
  }

  render() {
    const { userPermission } = this.props;
    const { 
      curStaffItem, 
      staff, 
      isLoaded, 
      searchFilter, 
      newTaskWindow, 
      popup, 
      popupMsg, 
      view, 
      viewMode 
    } = this.state;

    return (
      isLoaded
        ? <section className={styles.sectionContainer}>
            <CSSTransition
              in={popup}
              timeout = {300}
              mountOnEnter={true}
              unmountOnExit={true}
              classNames={{
                appear: styles.appear,
                enter: styles.enter,
                enterActive: styles.enterActive,
                exit: styles.exit,
                exitActive: styles.exitActive,
              }}>
                <Popup 
                  msg={popupMsg} 
                  closeClick={this.closePopup} 
                  okClick={this.deleteUser.bind(null, curStaffItem.id)}/>
            </CSSTransition>
            <CSSTransition
              in={newTaskWindow}
              timeout = {300}
              mountOnEnter={true}
              unmountOnExit={true}
              classNames={{
                appear: styles.appear,
                enter: styles.enter,
                enterActive: styles.enterActive,
                exit: styles.exit,
                exitActive: styles.exitActive,
              }}>
                <NewTaskForm 
                  mode={viewMode} 
                  taskData={{ executorId: curStaffItem.id }} 
                  onClose={this.closeTaskWindow}/>
            </CSSTransition>
            <SectionTitle children='Сотрудники' size='m'/>
            <header className={styles.header}>
              {
                view !== 'userList'
                  ? <div className={styles.returnBlock}>
                      <a className={styles.returnLink} 
                        onClick={this.returnToList} 
                        href=' ' 
                        children='Вернуться к списку'/>
                    </div>
                  : null
              }
              <div className={`${styles.searchBlock} ${view !== 'userList' ? styles.disabled : null}`}>
                <Input type='text'
                  name='search'
                  value={searchFilter}
                  readonly={false}
                  placeholder='Поиск'
                  onChange={this.onInputChange}
                />
              </div>
              <div className={`${styles.addButtonBlock} ${view !== 'userList' ? styles.disabled : null}`} 
                data-tooltip="Добавить&#10;нового&#10;сотрудника">
                  <AddButton onClick={this.setStaffMode.bind(null, 'profile', 'create')}/>
              </div>
            </header>
            {
              view === 'profile'
                ? <div className={styles.personalFormWrap}>
                    <SectionTitle children={titles[viewMode]} size='s'/>
                    <PersonalForm permission='manager' mode={viewMode} userId={curStaffItem.id}/>
                  </div>
                : view === 'userList'
                  ? staff.length
                    ? <UserList list={staff} 
                        activities={{
                        createUserTask: this.openTaskWindow.bind(null, 'create'),
                        deleteUser: this.deleteUser,
                        openPopup: this.openPopup,
                        closePopup: this.closePopup,
                        updateTaskWindow: this.openTaskWindow.bind(null, 'update'),
                        updateUser: this.setStaffMode.bind(null, 'profile', 'update'),
                        getTaskList: this.setStaffMode.bind(null, 'taskList', null)
                      }}/>
                    : <Cap text='Сотрудников не найдено'/>
                  : <div className={styles.dashboardWrap}>
                    <Dashboard 
                      userId={curStaffItem.id} 
                      userName={`${curStaffItem.firstName} ${curStaffItem.lastName}`} 
                      userPermission={userPermission}/>
                  </div>
            }
          </section>
      : <Preloader/>)
  }
}

export default Staff;